import React, { useState } from 'react';                    // REACT
import { withRouter } from "react-router-dom";              // ROUTER
import { useSelector, useDispatch } from 'react-redux';     // REDUX

import { connect } from '../../actions/connexion.action';   // ACTION

import Row from 'react-bootstrap/Row';              // BOOTSTRAP
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';

import FormLabel from '@material-ui/core/FormLabel';    // MATERIAL-UI
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';

import backgroundImage from '../../publics/Back_Connexion.png';

import { Formik } from 'formik';            // FORMIK
import * as Yup from 'yup';                 // YUP

import styles from './Connexion.module.css'   // CSS

function ConnexionContainer(props) {

  const dispatch = useDispatch();                         // create dispatcher

  const connectionValidationSchema = Yup.object().shape({           // validation schema
    login: Yup.string().required('Ce champ est obligatoire'),
    password: Yup.string().required('Ce champ est obligatoire'),
  });

  const handleSubmitForm = async (values, {setSubmitting, resetForm}) => {      // submit form
    setSubmitting(true);
    console.log('SUBMIT VALUES : '+JSON.stringify(values, null, 2));
    dispatch(connect(values.login, values.password));
    props.history.push(props.connexionRedirectPath);
    setSubmitting(false);
  }

  const [showPassword, setShowPassword] = React.useState(false);            // Show password state

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  console.log('The connexion container, props : '+props.connexionRedirectPath);

  return(
    <Container fluid>
      <Row className={ styles.globalContainer }>

        <Col md={{span: 7, offset: 0}} className={ styles.leftImage } style={{padding: '0px'}}>        {/* left image */}
          <div className={styles.svgDiv}>
            <img className={styles.svg} src={backgroundImage} alt="KikleoWelcomeImage" />
          </div>
        </Col>

        <Col md={{span: 5, offset: 0}}>         {/* rigth form */}
          <div className={styles.centerComponent}>
            <Row className="justify-content-md-center">
              <Col>
                <div className={styles.centerText}>
                  <h1 className={styles.title}> BIENVENUE </h1>                                           {/* title */}
                  <p className={styles.subtitle}> Connectez vous pour accder  vos donnes </p>
                </div>
              </Col>
            </Row>

            <br/>
                                                                                                        {/* form */}
            <Formik
              enableReinitialize
              initialValues={ { login: '',
                                password: '' } }
              validationSchema={ connectionValidationSchema }
              onSubmit={ handleSubmitForm }
            >
              { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => {
              return(
                <div>
                  <Row className="justify-content-md-center">                                          {/* login */}
                    <Col md={{span: 8, offset: 0}}>
                      <div className={ styles.formElement }>
                        <TextField id='login'
                                  name='login'
                                  label="Nom d'utilisateur"
                                  value={ values.login }
                                  helperText={ errors.login && touched.login ? errors.login : '' }
                                  error={ errors.login && touched.login ? true : false }
                                  onChange={ handleChange }
                                  onBlur={ handleBlur }
                                  fullWidth
                                  style={{color: '#666666'}}
                        />
                      </div>
                    </Col>
                  </Row>

                  <Row className="justify-content-md-center">                                          {/* password */}
                    <Col md={{span: 8, offset: 0}}>
                      <div className={ styles.formElement }>
                        <TextField id='password'
                                  type={showPassword ? 'text' : 'password'}
                                  name='password'
                                  label="Mot de passe"
                                  value={ values.password }
                                  helperText={ errors.password && touched.password ? errors.password : '' }
                                  error={ errors.password && touched.password ? true : false }
                                  onChange={ handleChange }
                                  onBlur={ handleBlur }
                                  fullWidth
                                  className={ styles.formElement }
                                  InputProps={{
                                    endAdornment:
                                      <InputAdornment position="end">
                                        <IconButton
                                          aria-label="toggle password visibility"
                                          onClick={handleClickShowPassword}
                                          onMouseDown={handleMouseDownPassword}
                                        >
                                          {showPassword ? <VisibilityIcon /> : <VisibilityOffIcon />}
                                        </IconButton>
                                      </InputAdornment>
                                  }}

                        />
                      </div>
                    </Col>
                  </Row>

                  <br/>

                  <Row style={{visibility: 'hidden'}} className="justify-content-md-center">
                    <Col md={{span: 3, offset: 0}}>
                      <span className={styles.centerText}>
                        Se souvenir de moi
                      </span>
                    </Col>
                    <Col md={{span: 3, offset: 2}}>
                      <span className={styles.centerText}>
                        Mot de passe oubli
                      </span>
                    </Col>
                  </Row>

                  <br/>

                  <Row className='row justify-content-center'>
                      <Button className={styles.submitButton} style={{backgroundColor: '#90c261', border: '#90c261', borderRadius: '5px', fontFamily: 'Muli', fontSize: '18px'}} type='submit' onClick={handleSubmit} disabled={isSubmitting}> Se connecter </Button>
                  </Row>
                </div>
              ) } }
            </Formik>
          </div>

        </Col>

      </Row>
    </Container>
  )
}

export default withRouter(ConnexionContainer);
