import React, { useEffect }from 'react';
import { Switch, Route, withRouter } from "react-router-dom";   //ROUTER
import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX

import Row from 'react-bootstrap/Row'   //BOOTSTRAP
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'

import ClientSideBar from '../topBar/ClientSideBar';    //COMPONENTS
import ClientTopBar from '../topBar/ClientTopBar';
import ClientDashboard from '../dashboard/ClientDashboard';
import AdminGaspi from '../../administration/gaspillage/AdminGaspi';
import ClientAccount from '../account/ClientAccount';

import styles from './ClientContainer.module.css'; //CSS

function ClientContainer(props) {

  useEffect( () => {

  });

  const displayPage = (pageMode) => {
    console.log('IN DISPLAY PAGE');
    switch (pageMode) {
      case 'dashboard':
        console.log('IN dashboard');
        return <ClientDashboard restaurantName={props.currentRestaurantName}/>;
        break;
      case 'stat':
        console.log('IN STAT');
        return <AdminGaspi restaurantName={props.currentRestaurantName} />;
        break;
      case 'advice':
        console.log('IN advice');
        return <h1>NOT READY YET</h1>;
        break;
      case 'restaurant':
        return (<h1>NOT READY YET</h1>);
        break;
      case 'account':
        return <ClientAccount />;
        break;
      default:
        return (<ClientDashboard />);
    }
  }

  console.log('BEFORE RENDERING !');

  return(
    <>
      <Row style={{ height:'100vh', margin: 0, padding: 0}}>
        <Col md={{span: 2, offset: 0}} className='fixed-top' style={{ padding: 0}}>
          <ClientSideBar />
        </Col>

        <Col md={{span: 10, offset: 2}}>
          <Row>
            <Col style={{ margin: 0, padding: 0}}>
              <ClientTopBar />
            </Col>
          </Row>
          <Row className={styles.ContentBigRow}>
            <Col>
              <div className={styles.ClientTop}>
              </div>
              <Row className={styles.ContentDiv + "justify-content-md-center"}>
                {
                  displayPage(props.pageMode)
                }
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  )
}

function mapStateToProps(state) {
  console.log('IN container map to state : '+state.clientReducer.page);
  return {
    pageMode: state.clientReducer.page,
    currentRestaurantName: state.navigationReducer.currentRestaurantName,
  }
}

export default connect(mapStateToProps)(ClientContainer);
