import React, { useState} from 'react';              //REACT
import { useSelector, useDispatch, connect } from 'react-redux';    //REDUX
import { changePageAction } from '../../../actions/client.action';  //ACTIONS

import { updateUserPassword } from '../../../services/AdminAPI';  //API

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import Row from 'react-bootstrap/Row';   //BOOTSTRAP
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

import styles from './Account.module.css'   // CSS

function ClientAccount(props) {

  const dispatch = useDispatch();

  const [updatePassword, setUpdatePassword] = useState(false);    //to know when to display the form

  const updatePasswordValidationSchema = Yup.object().shape({
    oldPassword: Yup.string().required('Ce champ est obligatoire'),
    newPassword: Yup.string().required('Ce champ est obligatoire'),
  });

  const changePage = (newPage) => {             //change the current page ( dashboard / stats / my restaurant / etc )
    dispatch(changePageAction(newPage));
  }

  const displayUpdatePassword = () => {          //display or hide the update password form
    setUpdatePassword(!updatePassword);
  };

  const handlePassword = async (values) => {
    console.log('In handle submit');
    //setSubmitting(true);
    updateUserPassword(values.oldPassword, values.newPassword, props.userState.login)
      .then( res => {
        console.log('In then update password : '+JSON.stringify(res, null, 2));
        //dispatch(updatePassword());
      })
      .catch( err => {
        console.log('ERROR ERROR : '+JSON.stringify(err, null, 2));
      })
    //setSubmitting(false);
  }

  return(
    <Col className={styles.globalContainer}>
      <Row>
        <Col md={{span: 12}}>
          <Row className="justify-content-md-center">
            <h1 className={styles.pageTitle}> Compte de {props.userState.login} </h1>
          </Row>
        </Col>
      </Row>

      <Formik
        enableReinitialize
        initialValues={ { oldPassword: '',
                          newPassword: '' } }
        validationSchema={ updatePasswordValidationSchema }
        onSubmit={ handlePassword }
      >
      { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => {
      return(
        <div>
          <Form onSubmit={ handleSubmit }>
            <Form.Row>
              <Form.Group as={ Col } md='4' controlId={ 'oldPassword' }>                   { /*OLD PASSWORD*/ }
                <Form.Label> Mot de passe actuel {errors.oldPassword} </Form.Label>
                <Form.Control isValid={ touched.oldPassword && !errors.oldPassword }
                              isInvalid={ touched.oldPassword && errors.oldPassword }
                              placeholder=''
                              onBlur={ handleBlur }
                              value={ values.oldPassword }
                              onChange={ handleChange }>
                </Form.Control>
              </Form.Group>

              <Form.Group as={ Col } md='4' controlId={ 'newPassword' }>                   { /*NEW PASSWORD*/ }
                <Form.Label> Nouveau mot de passe {errors.newPassword} </Form.Label>
                <Form.Control isValid={ touched.newPassword && !errors.newPassword }
                              isInvalid={ touched.newPassword && errors.newPassword }
                              placeholder=''
                              onBlur={ handleBlur }
                              value={ values.newPassword }
                              onChange={ handleChange }>
                </Form.Control>
              </Form.Group>
            </Form.Row>

            <Row className='row justify-content-center'>
              <Col md='8'>
                <Button variant='primary' type='submit' disabled={isSubmitting}> Valider </Button>
              </Col>
            </Row>
          </Form>
        </div>
      ) } }
      </Formik>
    </Col>
  )
}

function mapStateToProps(state) {
  return {
    userState: state.userReducer,
  }
}

export default connect(mapStateToProps)(ClientAccount);
