import React from 'react';

import { LoginRoute } from './helper/routeHelper'; //HELPERS
import PrivateClientRoute from './helper/PrivateClientRoute';
import PrivateAdminRoute from './helper/PrivateAdminRoute';

import Display from './components/Display';                                 //COMPONENTS
import ConnexionContainer from './components/connexion/Connexion';
import AdminContainer from './components/administration/container/AdminContainer';
import ClientContainer from './components/client/container/ClientContainer';

import { Switch, Route, withRouter } from "react-router-dom";   //ROUTER
import { useSelector, useDispatch } from 'react-redux';         //REDUX

import { increment } from './actions/admin.action';             //ACTIONS

function App() {

  return (
    <>
      <Switch>
        <Route exact path='/connexion/admin'>
          <LoginRoute component={ <ConnexionContainer connexionRedirectPath='/adminConsole' /> } />
        </Route>
        <Route exact path='/connexion/client'>
           <LoginRoute component={ <ConnexionContainer connexionRedirectPath='/clientConsole' /> } />
        </Route>

        <Route exact path='/adminConsole'>
           <PrivateAdminRoute component={ <AdminContainer /> } />
        </Route>
        <Route exact path='/clientConsole'>
           <PrivateClientRoute component={ <ClientContainer /> } />
        </Route>

        <Route path="/">
          <LoginRoute component={ <ConnexionContainer connexionRedirectPath='/clientConsole' /> } />
        </Route>
      </Switch>
    </>
  );
}

export default withRouter(App);
