import React, { useEffect } from 'react';

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX

import { Redirect } from 'react-router-dom';

import { getRedirectionAdminPage } from './routeHelper';
import { isAdmin, isClient, isLoggedIn } from './authorizationHelper';


const PrivateAdminRoute = (props) => {
  console.log('In private admin route');

  useEffect( () => {
    console.log('In useEffect private admin route');
    if (!props.userState.isLogged) {
      console.log('before get redirect admin : '+JSON.stringify(props.userState, null, 2));
      getRedirectionAdminPage();
    }
  });

  const getComponentToDisplay = () => {
    console.log('In get component to display in priv route admin');
    const redirection = props.userState.redirection;
    const connecting = props.userState.isLogging;
    const userType = props.userState.userType;
    console.log('Redirection is : _'+redirection+'_');

    if (connecting) {
      console.log('Must display wait for check');
      return (<h1> WAITING TO CHECK CREDENTIALS </h1>);
    }
    else if (userType==='admin') {
      console.log('The user is a chef');
      return props.component;
    }
    else {
      if (redirection == null) {
        return (<h1> WAITING TO CHECK CREDENTIALS </h1>);
      }
      else {
        console.log('Must display : '+redirection);
        return <Redirect to={redirection} push/>;
      }

    }
  }

  return (
    <>
      {
        getComponentToDisplay()
      }
    </>
  )
};

function mapStateToProps(state) {
  console.log('IN private admin route map to state : '+JSON.stringify(state.userReducer, null, 2));
  return {
    userState: state.userReducer,
  }
}

export default connect(mapStateToProps)(PrivateAdminRoute);
