import React from 'react';                    // REACT
import styles from './Form.module.css';       //CSS

import Form from 'react-bootstrap/Form';      //BOOTSTRAP

function FormCheckboxElement(props) {

  return(
    <Form.Check type='checkbox'
                className={styles.formCheck}
                id={'checkbox-'+props.id}
                name={ props.name }

    >
      <Form.Check.Input type='checkbox'
                        name={ props.name }
                        value={ props.value }
                        onChange={ props.onChange }
      />
      <Form.Check.Label name={ props.name } className={styles.formLabel}> {props.label} </Form.Check.Label>
    </Form.Check>
  )
}

export default FormCheckboxElement;
