import React, { useState, useEffect } from 'react';                 //REACT

import { useSelector, useDispatch, connect } from 'react-redux';    //REDUX
import { getContractsSuccess, getContractsError } from '../../../actions/admin.action';    //ACTIONS

import { getAllContracts } from '../../../services/AdminAPI';          //API FUNCTIONS

import ErrorFormField from '../../Common/ErrorFormField/ErrorFormField';   //COMPONENTS
import ContractForm from './ContractForm';
import ContractDetail from './ContractDetail';

import { Button, Form, Col, Row } from 'react-bootstrap';  //BOOTSTRAP

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import styles from './ClientDetails.module.css'

function ClientDetails(props) {

  const dispatch = useDispatch();

  const [addContrat, setAddContrat] = useState(false);

  const client = props.clientList[props.currentClient];

  const validationSchema = Yup.object().shape({
    NameClient: Yup.string().required('Ce champ est obligatoire'),
    adressClient: Yup.string().required('Ce champ est obligatoire'),
    emailClient: Yup.string().required('Ce champ est obligatoire'),
    phoneClient: Yup.string().required('Ce champ est obligatoire'),
    dateRegistrationClient: Yup.date().required('Ce champ est obligatoire')
  });

  const handleSubmit = async (values, {setSubmitting, resetForm}) => {          //HANDLE SUBMIT
   console.log('IN submit');
 }

  useEffect( () => {
    console.log('USE EFFECT CLIENT DETAILS');
    if (client.contractList == null) {
      getAllContracts(client.NameClient)
        .then( res => {
          dispatch(getContractsSuccess(res, props.clientList, props.currentClient));
        })
        .catch( err => {
          dispatch(getContractsError(err));
        })
    }
  });

  console.log('currentClient.contractList : '+JSON.stringify(props.clientList[props.currentClient].contractList, null, 2));

  return(
    <div className={ styles.detailsContainer }>
      <h1>Details : { client.NameClient } </h1>

      <Formik
        enableReinitialize
        initialValues={ { NameClient: client.NameClient,
                          adressClient: client.adressClient,
                          emailClient: client.emailClient,
                          phoneClient: client.phoneClient,
                          dateRegistrationClient: client.dateRegistrationClient } }
        validationSchema={ validationSchema }
        onSubmit={ handleSubmit }
      >
        { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) =>{
        return(
          <div>
            <Form as={Col} md='6' className={ styles.formContainer } onSubmit={ handleSubmit }>
              <Form.Row>
                <Form.Group as={ Col } md='6' controlId={ 'NameClientInput' }>                   { /*NAME*/ }
                  <Form.Label> Nom </Form.Label>
                  <Form.Control disabled
                                isValid={ touched.NameClient && !errors.NameClient }
                                isInvalid={ touched.NameClient && errors.NameClient }
                                placeholder='Nom du client'
                                onBlur={ handleBlur }
                                value={ values.NameClient }
                                onChange={ handleChange }>
                  </Form.Control>
                  <ErrorFormField touched={touched.societe} message={errors.societe} />
                </Form.Group>

                <Form.Group as={ Col } md='6' controlId={ 'adressClientInput' }>                   { /*ADRESS*/ }
                  <Form.Label> Adresse </Form.Label>
                  <Form.Control disabled
                                isValid={ touched.adressClient && !errors.adressClient }
                                isInvalid={ touched.adressClient && errors.adressClient }
                                placeholder='Adresse du client'
                                onBlur={ handleBlur }
                                value={ values.adressClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Form.Row>
                <Form.Group as={ Col } md='6' controlId={ 'emailClientInput' }>                  { /*EMAIL*/ }
                  <Form.Label> Email </Form.Label>
                  <Form.Control disabled
                                isValid={ touched.emailClient && !errors.emailClient }
                                isInvalid={ touched.emailClient && errors.emailClient }
                                placeholder='Email du client'
                                onBlur={ handleBlur }
                                value={ values.emailClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>

                <Form.Group as={ Col } md='6' controlId={ 'phoneClientInput' }>                  { /*PHONE*/ }
                  <Form.Label> Tlphone </Form.Label>
                  <Form.Control disabled
                                isValid={ touched.phoneClient && !errors.phoneClient }
                                isInvalid={ touched.phoneClient && errors.phoneClient }
                                placeholder='Email du client'
                                onBlur={ handleBlur }
                                value={ values.phoneClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Form.Row >
                <Form.Group as={ Col } md='4' controlId={ 'dateRegistrationClientInput' }>                 { /*DATE REGISTRATION*/ }
                  <Form.Label> Date d'enregistrement </Form.Label>
                  <Form.Control disabled
                                type='date'
                                isValid={ touched.dateRegistrationClient && !errors.dateRegistrationClient }
                                isInvalid={ touched.dateRegistrationClient && errors.dateRegistrationClient }
                                placeholder='Email du client'
                                onBlur={ handleBlur }
                                value={ values.dateRegistrationClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>
            </Form>
          </div>
        ) } }
      </Formik>

      <br/>

      <Row className='justify-content-md-center'>
        <Col md='2'>
          <h1> Contrats </h1>
        </Col>
      </Row>
      <Row>
        <Col md={{ offset: 8 }}>
          <Button onClick={ () => setAddContrat(!addContrat) }> Ajouter un contrat </Button>
        </Col>
      </Row>

      <Row>
        {
          addContrat ?
          <div>
            <ContractForm />
          </div>
          : <> </>
        }
      </Row>

      {
        props.clientList[props.currentClient].contractList != undefined ?
        //console.log("the client ocntract list")
        props.clientList[props.currentClient].contractList.map( (item, index) => {
          return (
            <>
              <ContractDetail contractIndex={index}/>
            </>
          )
        })
        : <> CONTRAT IS UNDEFINED </>
      }

    </div>
  )
}

function mapStateToProps(state) {
  console.log('IN STATEMAP CLIENT DETAILS, currentClient.contractList : '+state.adminReducer.clientList[state.adminReducer.currentClient].contractList);
  return {
    clientList: state.adminReducer.clientList,
    currentClient: state.adminReducer.currentClient,
    contractList: state.adminReducer.clientList[state.adminReducer.currentClient].contractList
  }
}

export default connect(mapStateToProps)(ClientDetails);
