export const connect = (login, password) => {
  return {
    type: 'CONNECTING',
    payload: {
      login: login,
      password: password,
    }
  };
};

export const failConnect = () => {
  return {
    type: 'FAILED_CONNECT',
  };
};

export const updateRedirection = (redirection) => {
  return {
    type: 'UPDATE_REDIRECTION',
    payload: {
      redirection: redirection,
    }
  };
};

export const logged = (userType) => {
  return {
    type: 'LOGGED',
    payload: {
      userType: userType,
    }
  };
};

export const disconnectAction = () => {
  return {
    type: 'DISCONNECT'
  };
};
