const initialState = {
  clientList: [],
  currentClient: null,
  fetching: false,
  fetched: false,
  error: null,
  adding: false,
  pageMode: 'createClient',
  currentRestaurant: null,
}

const adminReducer = (state = initialState, action) => {
  switch(action.type){

    //----------------------------- GET CLIENTS --------------------------------------

    case 'GET_ALL_CLIENTS_START':
    console.log('IN ADMIN REDUCER')
      return {
        ...state,
        fetching: true
      };
      break;

    case 'GET_ALL_CLIENTS_SUCCESS':
    console.log('In get all client success');
      const mode = action.data.length != 0 ? 'displayClient' : 'createClient';
      return {
        ...state,
        fetching: false,
        fetched: true,
        clientList: action.data,
        currentClient: 0,
        pageMode: mode
      };
      break;

    case 'GET_ALL_CLIENTS_ERROR':
      console.log('In get all client error');
      return {
        ...state,
        fetching: false,
        fetched: false,
        error: action.error
      }
      break;

    case 'CHANGE_CURRENT_CLIENT':
    console.log('IN ADMIN REDUCER')

      return {
        ...state,
        currentClient: action.clientIndex,
        pageMode: 'displayClient'
      }
      break;

//----------------------------- CREATE CLIENTS --------------------------------------

    case 'CREATE_CLIENT_START':
      console.log('IN ADMIN REDUCER')

      return {
        ...state,
        adding: true
      };
      break;

    case 'CREATE_CLIENT_SUCCESS':
      console.log('IN ADMIN REDUCER')

      action.data.clientList.push(action.data.newClient)
      return {
        ...state,
        adding: false,
        clientList: action.data.clientList,
        currentClient: action.data.clientList.length -1,
        pageMode: 'displayClient'
      };
      break;

    case 'CREATE_CLIENT_ERROR':
      return {
        ...state,
        adding: false,
        error: action.error
      };
      break;

    case 'CREATE_MODE_ACTIVATED':
      console.log('IN REDUCER CREATE client MODE')
      return {
        ...state,
        pageMode: 'createClient'
      };
      break;

//----------------------------- GET CONTRACTS --------------------------------------

    case 'GET_CONTRACTS_SUCCESS':
    console.log('IN ADMIN REDUCER')

      action.clientList[action.currentClient].contractList = action.data.Items;
      return {
        ...state,
        clientList: action.clientList
      };
      break;

    case 'GET_CONTRACTS_ERROR':
    console.log('IN ADMIN REDUCER')

      return {
        ...state,
        error: action.error
      }
      break;

//----------------------------- CREATE CONTRACT --------------------------------------

    case 'CREATE_CONTRACT_SUCCESS':
    console.log('IN ADMIN REDUCER')

      action.clientList[action.currentClient].contractList.push(action.newContract);
      console.log(JSON.stringify(action.clientList, null, 2));
      return {
        ...state,
        clientList: action.clientList
      };
      break;

    case 'CREATE_CONTRACT_ERROR':
    console.log('IN ADMIN REDUCER')

      return {
        ...state,
        error: action.error
      };
      break;


//----------------------------- GET RESTAURANTS --------------------------------------

    case 'GET_RESTAURANTS_SUCCESS':
    console.log('IN ADMIN REDUCER')

      action.clientList[action.currentClient].contractList[action.currentContract].restaurantList = action.data.Items;
      return {
        ...state,
        clientList: action.clientList
      };
      break;

    case 'GET_RESTAURANTS_ERROR':
    console.log('IN ADMIN REDUCER')

      return {
        ...state,
        error: action.error
      }
      break;

//----------------------------- CREATE RESTAURANT --------------------------------------

    case 'CREATE_RESTAURANT_SUCCESS':
    console.log('IN ADMIN REDUCER')

      action.clientList[action.currentClient].contractList[action.currentContract].restaurantList.push(action.newRestaurant);
      console.log(JSON.stringify(action.clientList, null, 2));
      return {
        ...state,
        clientList: action.clientList
      };
      break;

    case 'CREATE_RESTAURANT_ERROR':
      console.log('IN ADMIN REDUCER')

      return {
        ...state,
        error: action.error
      };
      break;

//----------------------------- CHANGE CURRENT RESTAURANT --------------------------------------

    case 'CHANGE_CURRENT_RESTAURANT':
    console.log('IN ADMIN REDUCER')

      return {
        ...state,
        currentRestaurant: action.data
      };
      break;

//----------------------------- CHANGE CURRENT PAGE MODE --------------------------------------

    case 'CHANGE_PAGE_MODE':
      console.log('IN REDUCER CHANGE PAGE MODE : '+action.data);

      return {
        ...state,
        pageMode: action.data
      };
      break;

//----------------------------- CHANGE CURRENT PAGE MODE --------------------------------------

    case 'UPDATE_RESTAURANT':
      console.log('IN REDUCER update restaurant : '+action.data);
      action.data.clientList[action.data.client].contractList[action.data.contract].restaurantList[action.data.restaurant].accountCreated = true;
      return {
        ...state,
        clientList: action.data.clientList
      };
      break;

    case 'DISCONNECT':
      console.log('In disconnect admin')
      return initialState;
      break;

    default:
      return state; break;
  }
}

export default adminReducer;
