import React from 'react';                    // REACT
import styles from './Form.module.css';       //CSS

import Form from 'react-bootstrap/Form';       //BOOTSTRAP

function FormRadioElement(props) {

  return(
    <Form.Check type='radio'
                className={styles.formCheck}
                id='line'

    >
      <Form.Check.Input type='radio'
                        name={props.name}
                        checked={ props.currentValue == props.value }
                        onChange={ props.onChange }
                        onClick= { props.onClick }
      />
      <Form.Check.Label className={styles.formLabel}> {props.label} </Form.Check.Label>
    </Form.Check>
  )
}

export default FormRadioElement;
