import React from 'react';                    // REACT

import Row from 'react-bootstrap/Row';              // BOOTSTRAP
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';

import styles from './DashboardElement.module.css'   // CSS

function DashboardElement(props) {

  console.log('The dashboard element, props : '+props.connexionRedirectPath);

  return(
    <div className={styles.global}>

        <div style={{color: props.color, height: '20%'}}>
          <div className={styles.title}>
            {props.title}
          </div>
        </div>

        <div className={styles.centerComponent} style={{color: props.color}}>
          <div className={styles.value}>
            {props.value}
          </div>
          <div className={styles.legend}>
            {props.legend}
          </div>
        </div>

    </div>
  )
}

export default DashboardElement;

