import { getAllClients } from '../services/AdminAPI';

// ----------------------------- GET ALL THE CLIENTS ---------------------------------------

export function getAllClientStart () {
  return { type: 'GET_ALL_CLIENTS_START' };
}

export function getAllClientsSuccess (data) {
  return {
        type: 'GET_ALL_CLIENTS_SUCCESS',
        data: data
      };
}

export function getAllClientsError (error) {
  return {
        type: 'GET_ALL_CLIENTS_ERROR',
        error: error
      };
}

export function changeCurrentClient (newCurrentClientIndex) {
  return {
        type: 'CHANGE_CURRENT_CLIENT',
        clientIndex: newCurrentClientIndex
      };
}

// ----------------------------- CREATE A NEW CLIENT ---------------------------------------

export function changeModeToCreate() {
  return { type: 'CREATE_MODE_ACTIVATED' };
}

export function createClientStart () {
  return { type: 'CREATE_CLIENT_START' };
}

export function createClientSuccess (clientList, newClient) {
  return {
          type: 'CREATE_CLIENT_SUCCESS',
          data: { clientList: clientList,
                  newClient: newClient
                }
        };
}

export function createClientError (error) {
  return {
          type: 'CREATE_CLIENT_ERROR',
          error: error
         };
}

// ----------------------------- GET ALL THE CONTRACTS FOR A CLIENT ---------------------------------------

export function getContractsSuccess (data, clientList, currentClient) {
  return {
        type: 'GET_CONTRACTS_SUCCESS',
        data: data,
        clientList: clientList,
        currentClient: currentClient
      };
}

export function getContractsError (error) {
  return {
        type: 'GET_CONTRACTS_ERROR',
        error: error
      };
}

// ----------------------------- CREATE A NEW CONTRACT FOR A CLIENT ---------------------------------------

export function createContractSuccess (newContract, clientList, currentClient) {
  return {
          type: 'CREATE_CONTRACT_SUCCESS',
          newContract: newContract,
          clientList: clientList,
          currentClient: currentClient
        };
}

export function createContractError (error) {
  return {
          type: 'CREATE_CONTRACT_ERROR',
          error: error
         };
}

// ----------------------------- GET ALL THE RESTAURANTS FOR A CONTRACT ---------------------------------------

export function getRestaurantsSuccess (data, clientList, currentClient, currentContract) {
  return {
        type: 'GET_RESTAURANTS_SUCCESS',
        data: data,
        clientList: clientList,
        currentClient: currentClient,
        currentContract: currentContract
      };
}

export function getRestaurantsError (error) {
  return {
        type: 'GET_RESTAURANTS_ERROR',
        error: error
      };
}

// ----------------------------- CREATE A NEW RESTAURANT FOR A CONTRACT ---------------------------------------

export function createRestaurantSuccess (newRestaurant, clientList, currentClient, currentContract) {
  return {
          type: 'CREATE_RESTAURANT_SUCCESS',
          newRestaurant: newRestaurant,
          clientList: clientList,
          currentClient: currentClient,
          currentContract: currentContract
        };
}

export function createRestaurantError (error) {
  return {
          type: 'CREATE_RESTAURANT_ERROR',
          error: error
         };
}

//------------------------------------- CHANGE THE CURRENT RESTAURANT ----------------------

export function changeCurrentRestaurant(newCurrentRestaurantName) {
  return {
    type: 'CHANGE_CURRENT_RESTAURANT',
    data: newCurrentRestaurantName
  }
}

//------------------------------------- CHANGE THE PAGE MODE ----------------------

export function changePageMode(newMode) {
  return {
    type: 'CHANGE_PAGE_MODE',
    data: newMode
  }
}

//------------------------------------- UPDATE RESTAURANT ----------------------

export function updateRestaurant(client, contract, restaurant, clientList) {
  return {
    type: 'UPDATE_RESTAURANT',
    data: {
      client: client,
      contract: contract,
      restaurant: restaurant,
      clientList: clientList
    }
  }
}
