import React from 'react';
import { ResponsiveBar } from '@nivo/bar';

// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const MyResponsiveBar = (props) => {

  console.log('IN RENDER BAR !');
  console.log('the BAR data : '+JSON.stringify(props.data, null, 2));
  console.log('the BAR keys : '+JSON.stringify(props.keys, null, 2));
  console.log('the BAR index : '+JSON.stringify(props.index, null, 2));
  console.log('the BAR ALL DATA : '+JSON.stringify(props.allData, null, 2));


  //const keys = [props.keys.map( item => { return item.id } )];

  const toolTipElement = (node) => {
    console.log('IN TOOLTIP : '+JSON.stringify(node, null, 2));
    var foodIndex = props.allData.map( item => item.id ).indexOf(node.id);
    console.log('FOOD INDEX  = '+foodIndex);
    var dateIndex = props.allData[foodIndex].data.map( item => item.x ).indexOf(node.indexValue);
    console.log('THE INDEX : ' + dateIndex);

    return (
      <>
      <div>
        { node.value }
      </div>
      {
        /*
        <div style={{backgroundColor: 'white', display: 'none'}}>
          <> { node.id } </>
          <br/>
          <>{ node.indexValue } : </> <> { node.value } </>
          <br/>
          <> Moyenne : </> <> { node.value==0 ? 0 :(node.value / props.allData[foodIndex].data[dateIndex].tray).toFixed(2) } </>
        </div>
        */
      }
      </>
    )
  };

  const margin = () => {
    console.log('bar drilldown : '+props.drillDown);
    if (props.drillDown == 'category' || props.drillDown == 'day') {
      return { top: 30, right: 30, bottom: 70, left: 65 };
    }
    else {
      return { top: 30, right: 30, bottom: 50, left: 65 };
    }
  }

  const rotation = () => {
    console.log('bar drilldown : '+props.drillDown);
    if (props.drillDown == 'category' || props.drillDown == 'day') {
      return 15;
    }
    else {
      return 0;
    }
  }

  const getBarColor = () => {
    console.log('THE COLOR : '+JSON.stringify(props, null, ));
    return props.customColor;
  };

  return (
    <ResponsiveBar
        onClick={ (e) => props.handleClick(e) }
        data={ props.data }
        keys={ [props.keys] }
        indexBy={props.index}
        colors={getBarColor}
        borderRadius={5}
        padding={0.5}
        margin={margin()}
        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: '#38bcb2',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: '#eed312',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        fill={[
            {
                match: {
                    id: 'fries'
                },
                id: 'dots'
            },
            {
                match: {
                    id: 'sandwich'
                },
                id: 'lines'
            }
        ]}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: rotation(),
            legend: '',
            legendPosition: 'middle',
            legendOffset: 32
        }}
        axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: props.dataLegendLeftAxis,
            legendPosition: 'middle',
            legendOffset: -50
        }}
        labelFormat={d => <tspan y={ -10 }>{ d }</tspan>}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}

        animate={true}
        motionStiffness={90}
        motionDamping={15}
    />
  )
}

export default MyResponsiveBar;
