import React from 'react';              //REACT

import { useSelector, useDispatch, connect } from 'react-redux';    //REDUX

import { changePageMode } from '../../../actions/admin.action';

import Row from 'react-bootstrap/Row'   //BOOTSTRAP
import Col from 'react-bootstrap/Col'

import ClientList from '../home/ClientList';  //COMPONENTS

import styles from './AdminSideBar.module.css' //CSS

function AdminSideBar(props) {

  const dispatch = useDispatch();

  const changePage = (newPage) => {
    dispatch(changePageMode(newPage));
  };

  console.log('Before render adminSideBar');
  return(
    <Col md='12' className={styles.adminSideBarContainer}>
        <b> Kikleo </b>

        <div id='topbarTitle'>
          <b> ADMINISTRATION CONSOLE </b>
        </div>

        <div id='topbarDeconnexion'>
          Dconnexion
        </div>

        <ClientList clientList={ props.clientList }/>

        {
          props.currentRestaurant != null ?
            <>
              <div onClick={ () => changePage('dashboard') }>
                Tableau de bord
              </div>

              <div onClick={ () => changePage('stat') }>
                Mes statistiques
              </div>

              <div onClick={ () => changePage('advice') }>
                Mon espace conseil
              </div>

              <div onClick={ () => changePage('restaurant') }>
                Mon restaurant
              </div>
            </>
          :
            <>
            </>
        }
    </Col>
  )
}

function mapStateToProps(state) {
  console.log('IN adminSideBar map to state : '+state.adminReducer);
  return {
    clientList: state.adminReducer.clientList,
    currentRestaurant: state.adminReducer.currentRestaurant,
  }
}

export default connect(mapStateToProps)(AdminSideBar);
