import React from 'react';          //REACT

import ErrorFormField from '../../Common/ErrorFormField/ErrorFormField';   //COMPONENTS

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX
import { createClientStart, createClientSuccess, createClientError } from '../../../actions/admin.action';  //ACTIONS

import { createClient } from '../../../services/AdminAPI';           //API FUNCTIONS

import { Button, Form, Col, Row, Container } from 'react-bootstrap';  //BOOTSTRAP

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import styles from './ClientDetails.module.css' //CSS

function ClientForm() {

  const dispatch = useDispatch();
  const adminState = useSelector(state => state.adminReducer);
                                                                                //VALIDATION SCHEMA
  const validationSchema = Yup.object().shape({
    NameClient: Yup.string().required('Ce champ est obligatoire'),
    adressClient: Yup.string().required('Ce champ est obligatoire'),
    emailClient: Yup.string().required('Ce champ est obligatoire'),
    phoneClient: Yup.string().required('Ce champ est obligatoire'),
    dateRegistrationClient: Yup.date().required('Ce champ est obligatoire')
  });

  const handleSubmit = async (values, {setSubmitting, resetForm}) => {          //HANDLE SUBMIT
   setSubmitting(true);
   console.log('IN SUBMIIIIIIIIIIIIIIT !!!!!!!!!!!!!!!!!!!!!!!!');
   dispatch(createClientStart());
   createClient(values)
    .then( res => {
      dispatch(createClientSuccess(adminState.clientList, res));
    })
    .catch( err => {
      dispatch(createClientError(err));
    })
   resetForm();
   setSubmitting(false);
 }

  return(
    <div className={ styles.detailsContainer }>
      <h1>Create a new client </h1>

      <Formik
        initialValues={ { NameClient: '',
                          adressClient: '',
                          emailClient: '',
                          phoneClient: '',
                          dateRegistrationClient: Date.now() } }
        validationSchema={ validationSchema }
        onSubmit={ handleSubmit }
      >
        { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => (
          <div>
            <Form as={ Col } md='6' className={ styles.formContainer } onSubmit={ handleSubmit }>
              <Form.Row>
                <Form.Group as={ Col } md='6' controlId={ 'NameClientInput' }>                   { /*NAME*/ }
                  <Form.Label> Nom </Form.Label>
                  <Form.Control isValid = { touched.NameClient && !errors.NameClient }
                                isInvalid = { touched.NameClient && errors.NameClient }
                                name = 'NameClient'
                                placeholder = 'Nom du client'
                                onBlur = { handleBlur }
                                value = { values.NameClient }
                                onChange = { handleChange }>
                  </Form.Control>
                  <ErrorFormField touched = {touched.societe} message={errors.societe} />
                </Form.Group>

                <Form.Group as={ Col } md='6' controlId={ 'adressClientInput' }>                   { /*ADRESS*/ }
                  <Form.Label> Adresse </Form.Label>
                  <Form.Control isValid = { touched.adressClient && !errors.adressClient }
                                isInvalid = { touched.adressClient && errors.adressClient }
                                name = 'adressClient'
                                placeholder = 'Adresse du client'
                                onBlur = { handleBlur }
                                value = { values.adressClient }
                                onChange = { handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Form.Row>
                <Form.Group as={ Col } md='6' controlId={ 'emailClientInput' }>                  { /*EMAIL*/ }
                  <Form.Label> Email </Form.Label>
                  <Form.Control isValid={ touched.emailClient && !errors.emailClient }
                                isInvalid={ touched.emailClient && errors.emailClient }
                                name = 'emailClient'
                                placeholder='Email du client'
                                onBlur={ handleBlur }
                                value={ values.emailClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>

                <Form.Group as={ Col } md='6' controlId={ 'phoneClientInput' }>                  { /*PHONE*/ }
                  <Form.Label> Tlphone </Form.Label>
                  <Form.Control isValid={ touched.phoneClient && !errors.phoneClient }
                                isInvalid={ touched.phoneClient && errors.phoneClient }
                                name = 'phoneClient'
                                placeholder='Tlphone du client'
                                onBlur={ handleBlur }
                                value={ values.phoneClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Form.Row>
                <Form.Group as={ Col } md='6' controlId={ 'dateRegistrationClientInput' }>                 { /*DATE REGISTRATION*/ }
                  <Form.Label> Date d'enregistrement </Form.Label>
                  <Form.Control type='date'
                                isValid={ touched.dateRegistrationClient && !errors.dateRegistrationClient }
                                isInvalid={ touched.dateRegistrationClient && errors.dateRegistrationClient }
                                name = 'dateRegistrationClient'
                                placeholder=''
                                onBlur={ handleBlur }
                                value={ values.dateRegistrationClient }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Row className='row justify-content-center'>
                <Button variant='primary' type='submit' onClick={handleSubmit} disabled={isSubmitting}> Valider </Button>
              </Row>
            </Form>
          </div>
        ) }
      </Formik>
    </div>
  )
}

export default ClientForm;
