import React, { useState} from 'react';              //REACT
import { useSelector, useDispatch, connect } from 'react-redux';    //REDUX
import { changePageAction } from '../../../actions/client.action';  //ACTIONS
import { disconnectAction } from '../../../actions/connexion.action';  //ACTIONS

import { updateUserPassword } from '../../../services/AdminAPI';

import chef from '../../../publics/image.png';
import topSideBar from '../../../publics/TopSideBar.png';

import Row from 'react-bootstrap/Row';   //BOOTSTRAP
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import DropdownButton from 'react-bootstrap/DropdownButton';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Image from 'react-bootstrap/Image';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';


import SearchIcon from '@material-ui/icons/Search'; //Material-ui
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Divider from '@material-ui/core/Divider';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';

import styles from './ClientTopBar.module.css' //CSS

function ClientTopBar(props) {

  const dispatch = useDispatch();

  const [updatePassword, setUpdatePassword] = useState(false);
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');



  const changePage = (newPage) => {             //change the current page ( dashboard / stats / my restaurant / etc )
    dispatch(changePageAction(newPage));
  }

  const disconnect = () => {
    dispatch(disconnectAction());
  }



  return(
    <Navbar className={styles.ClientTopBarContainer+" justify-content-between"}>
      <div style={{visibility: 'hidden'}}>
      <Form inline >
       <InputGroup>
         <InputGroup.Prepend>
           <InputGroup.Text id="basic-addon1">@</InputGroup.Text>
         </InputGroup.Prepend>
         <FormControl
           placeholder="Username"
           aria-label="Username"
           aria-describedby="basic-addon1"
         />
       </InputGroup>
      </Form>
      </div>

      <Navbar.Collapse className="justify-content-end">
        <Navbar.Text className>{props.userState.login}</Navbar.Text>
        <NavDropdown title={<img className={styles.image} src={chef} alt="Logo" />} id="collasible-nav-dropdown" drop='left'>
          <NavDropdown.Item onClick={ () => changePage('account') }> Voir le profil </NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item onClick={ () => disconnect() }> Dconnexion </NavDropdown.Item>
        </NavDropdown>
      </Navbar.Collapse>
    </Navbar>
  )
}

function mapStateToProps(state) {
  return {
    userState: state.userReducer,
  }
}

export default connect(mapStateToProps)(ClientTopBar);


