import React from 'react';          //REACT

import ErrorFormField from '../../Common/ErrorFormField/ErrorFormField';   //COMPONENTS

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX
import { createRestaurantSuccess, createRestaurantError } from '../../../actions/admin.action';  //ACTIONS

import { createRestaurant } from '../../../services/AdminAPI';           //API FUNCTIONS

import { Button, Form, Col, Row, Container } from 'react-bootstrap';  //BOOTSTRAP

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import styles from './RestaurantForm.module.css';

function RestaurantForm(props) {

  const dispatch = useDispatch();
                                                                                //VALIDATION SCHEMA
  const restaurantValidationSchema = Yup.object().shape({
    nameRestaurant: Yup.string().required('Ce champ est obligatoire'),
    adressRestaurant: Yup.string().required('Ce champ est obligatoire'),
  });

  const handleAddRestaurant = async (values, {setSubmitting, resetForm}) => {          //HANDLE SUBMIT
    setSubmitting(true);
    console.log('IN SUBMIT NEW RESTAURANT')
    const contractName = props.clientList[props.currentClient].contractList[props.contractIndex].nameContract;
    values.contractName = contractName;
    createRestaurant(contractName, values)
      .then( res => {
        dispatch(createRestaurantSuccess(res, props.clientList, props.currentClient));
      })
      .catch( err => {
        dispatch(createRestaurantError(err));
      })
    setSubmitting(false);
 }

  return(
    <div className={ styles.newRestaurant }>
      <Row className='justify-content-md-center'>
        <h1>Cration d'un nouveau restaurant :</h1>
      </Row>

      <Formik
        enableReinitialize
        initialValues={ { nameRestaurant: '',
                          adressRestaurant: '' } }
        validationSchema={ restaurantValidationSchema }
        onSubmit={ handleAddRestaurant }
      >
        { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => {
        return(
          <div>
            <Form onSubmit={ handleSubmit }>
              <Form.Row className='justify-content-md-center'>
                <Form.Group as={ Col } md='4' controlId={ 'nameRestaurant' }>                   { /*NAME*/ }
                  <Form.Label> Nom </Form.Label>
                  <Form.Control isValid={ touched.nameRestaurant && !errors.nameRestaurant }
                                isInvalid={ touched.nameRestaurant && errors.nameRestaurant }
                                placeholder='Nom du restaurant'
                                onBlur={ handleBlur }
                                value={ values.nameRestaurant }
                                onChange={ handleChange }>
                  </Form.Control>
                  <ErrorFormField touched={touched.nameRestaurant} message={errors.nameRestaurant} />
                </Form.Group>

                <Form.Group as={ Col } md='4' controlId={ 'adressRestaurant' }>                   { /*ADRESS*/ }
                  <Form.Label> Adresse </Form.Label>
                  <Form.Control isValid={ touched.adressRestaurant && !errors.adressRestaurant }
                                isInvalid={ touched.adressRestaurant && errors.adressRestaurant }
                                placeholder='Adresse du restaurant'
                                onBlur={ handleBlur }
                                value={ values.adressRestaurant }
                                onChange={ handleChange }>
                  </Form.Control>
                </Form.Group>
              </Form.Row>

              <Row className='row justify-content-center'>
                <Button variant='primary' type='submit' disabled={isSubmitting}> Valider </Button>
              </Row>
            </Form>
          </div>
        ) } }
      </Formik>
    </div>
  )
}

function mapStateToProps(state) {
  return {
    clientList: state.adminReducer.clientList,
    currentClient: state.adminReducer.currentClient
  }
}

export default connect(mapStateToProps)(RestaurantForm);
