export function changePageAction (data) {
  return {
        type: 'CHANGE_PAGE',
        data: {
                newPage: data
              }
      };
};

export function getRawDataStart () {
  return { type: 'GET_RAW_DATA_START' };
};

export function getRawDataSuccess (data) {
  return {
        type: 'GET_RAW_DATA_SUCCESS',
        data: data
      };
};

export function getRawDataError (error) {
  return {
        type: 'GET_RAW_DATA_ERROR',
        error: error
      };
};

export function updateDrillDown (newLevel) {
  return {
        type: 'UPDATE_DRILLDOWN',
        data: newLevel
      };
};

export function updateSelectedMonth (newMonth) {
  return {
        type: 'UPDATE_MONTH',
        data: newMonth
      };
};

export function updateSelectedWeek (newWeek) {
  return {
        type: 'UPDATE_WEEK',
        data: newWeek
      };
};

export function updateSelectedDay (newDay) {
  return {
        type: 'UPDATE_DAY',
        data: newDay
      };
};

export function updateSelectedCategory (newCategory) {
  return {
        type: 'UPDATE_CATEGORY',
        data: newCategory
      };
};

export function updateDataType (newType) {
  return {
        type: 'UPDATE_DATA_TYPE',
        data: newType
      };
};
