import React from 'react';          //REACT

import ErrorFormField from '../../Common/ErrorFormField/ErrorFormField';   //COMPONENTS

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX
import { createContractSuccess, createContractError } from '../../../actions/admin.action';  //ACTIONS

import { createContract } from '../../../services/AdminAPI';           //API FUNCTIONS

import { Button, Form, Col, Row, Container } from 'react-bootstrap';  //BOOTSTRAP

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import styles from './ContractForm.module.css';

function ClientForm(props) {

  const dispatch = useDispatch();
  const adminState = useSelector(state => state.adminReducer);
                                                                                //VALIDATION SCHEMA
  const contratValidationSchema = Yup.object().shape({
    nameContract: Yup.string().required('Ce champ est obligatoire'),
    typeContract: Yup.string().required('Ce champ est obligatoire'),
    startDateContract: Yup.date().required('Ce champ est obligatoire')
  });

  const handleAddContrat = async (values, {setSubmitting, resetForm}) => {          //HANDLE SUBMIT
    setSubmitting(true);
    const currentName = props.clientList[props.currentClient].NameClient;
    values.clientName = currentName;
    console.log('contract VALUES : '+JSON.stringify(values, null, 2));
    createContract(currentName, values)
      .then( res => {
        dispatch(createContractSuccess(res, props.clientList, props.currentClient));
      })
      .catch( err => {
        dispatch(createContractError(err));
      })
    setSubmitting(false);
 }

  return(
    <div as={Col} md='9' className={styles.newContractContainer}>
      <Row>
        <Col>
          <h3> Crer un nouveau contrat </h3>
        </Col>
      </Row>

      <Row>
        <Col>
          <Formik
            enableReinitialize
            initialValues={ { nameContract: '',
                              typeContract: '',
                              startDateContract: '' } }
            validationSchema={ contratValidationSchema }
            onSubmit={ handleAddContrat }
          >
            { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => {
            return(
              <div>
                <Form className={styles.newContractForm} onSubmit={ handleSubmit }>
                  <Form.Row>
                    <Form.Group as={ Col } md='4' controlId={ 'nameContract' }>                   { /*NAME*/ }
                      <Form.Label> Nom </Form.Label>
                      <Form.Control isValid={ touched.nameContract && !errors.nameContract }
                                    isInvalid={ touched.nameContract && errors.nameContract }
                                    placeholder='Nom du contrat'
                                    onBlur={ handleBlur }
                                    value={ values.nameContract }
                                    onChange={ handleChange }>
                      </Form.Control>
                      <ErrorFormField touched={touched.societe} message={errors.societe} />
                    </Form.Group>

                    <Form.Group as={ Col } md='4' controlId={ 'typeContract' }>                   { /*TYPE*/ }
                      <Form.Label> Type </Form.Label>
                      <Form.Control isValid={ touched.typeContract && !errors.typeContract }
                                    isInvalid={ touched.typeContract && errors.typeContract }
                                    placeholder='Type du contrat'
                                    onBlur={ handleBlur }
                                    value={ values.typeContract }
                                    onChange={ handleChange }>
                      </Form.Control>
                    </Form.Group>

                    <Form.Group as={ Col } md='4' controlId={ 'startDateContract' }>                 { /*START DATE*/ }
                      <Form.Label> Date de dbut </Form.Label>
                      <Form.Control type='date'
                                    isValid={ touched.startDateContract && !errors.startDateContract }
                                    isInvalid={ touched.startDateContract && errors.startDateContract }
                                    placeholder='Date de dbut du contrat'
                                    onBlur={ handleBlur }
                                    value={ values.startDateContract }
                                    onChange={ handleChange }>
                      </Form.Control>
                    </Form.Group>
                  </Form.Row>

                  <Row className='row justify-content-center'>
                    <Button variant='primary' type='submit' disabled={isSubmitting}> Valider </Button>
                  </Row>
                </Form>
              </div>
            ) } }
          </Formik>
        </Col>
      </Row>
    </div>
  )
}

function mapStateToProps(state) {
  return {
    clientList: state.adminReducer.clientList,
    currentClient: state.adminReducer.currentClient
  }
}

export default connect(mapStateToProps)(ClientForm);
