import React, { useState, useEffect } from 'react';                  //REACT
import { withRouter } from "react-router-dom";                       //ROUTER
import { useSelector, useDispatch, connect } from 'react-redux';     //REDUX

import { getRestaurantsSuccess, getRestaurantsError } from '../../../actions/admin.action';    //ACTIONS

import { getAllRestaurants } from '../../../services/AdminAPI';          //API FUNCTIONS

import RestaurantForm from './RestaurantForm';  //COMPONENTS
import RestaurantDetail from './RestaurantDetail';

import Row from 'react-bootstrap/Row';       //BOOTSTRAP
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';

import styles from './ContractDetails.module.css';

function ContractDetail(props) {

  const dispatch = useDispatch();

  const contract = props.clientList[props.currentClient].contractList[props.contractIndex];

  const [addRestaurant, setAddRestaurant] = useState(false);

  useEffect( () => {
    console.log('USE EFFECT CONTRACT DETAILS');
    if (contract.restaurantList == null) {
      getAllRestaurants(contract.nameContract)
        .then( res => {
          dispatch(getRestaurantsSuccess(res, props.clientList, props.currentClient, props.contractIndex));
        })
        .catch( err => {
          dispatch(getRestaurantsError(err));
        })
    }
  });

  console.log('contract detail before rendering : '+JSON.stringify(props.clientList[props.currentClient].contractList[props.contractIndex].restaurantList, null, 2));

  return(
    <div className={styles.ContractDetails}>
      <Row>
        <Col md={{ offset: 1 }}>
          <h2> { contract.nameContract } </h2>
        </Col>
        <Col md={{ offset: 1 }}>
          <h2> { contract.typeContract } </h2>
        </Col>
        <Col md={{ offset: 1 }}>
          <h2> { contract.startDateContract } </h2>
        </Col>
      </Row>

      <br/>

      <Row className='justify-content-md-center'>
        <Col md='2'>
          <Button onClick={ () => setAddRestaurant(true) }> Ajouter un restaurant </Button>
        </Col>
      </Row>

      {
        addRestaurant ?
        <Row>
          <Col md='12'>
            <RestaurantForm contractIndex={props.contractIndex}/>
          </Col>
        </Row>
        : <> </>
      }

      {
        props.clientList[props.currentClient].contractList[props.contractIndex].restaurantList != undefined ?
        props.clientList[props.currentClient].contractList[props.contractIndex].restaurantList.map( (item, index) => {
          return (
            <div>
              <RestaurantDetail restaurantIndex={index} contractIndex={props.contractIndex}/>
            </div>
          )
        })
        : <> RESTAURANT LIST IS UNDEFINED </>
      }
    </div>
  )
}

function mapStateToProps(state, ownProps) {
  console.log('IN STATEMAP contract detail');
  return {
    clientList: state.adminReducer.clientList,
    currentClient: state.adminReducer.currentClient,
    restaurants: state.adminReducer.clientList[state.adminReducer.currentClient].contractList[ownProps.contractIndex].restaurantList
  }
}

export default connect(mapStateToProps)(ContractDetail);
