    /*const samples = getAllSamplesForRestaurantAndDay(restaurant, day.format('YYYY-MM-DD'));
    console.log('The samples : '+JSON.stringify(samples, null, 2));
    return samples;*/
    await getAllSamplesForRestaurantAndDay(restaurant, day.format('YYYY-MM-DD'))
