import React, { useEffect } from 'react';

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX
import applicationStore from '../store/store';            //store

import { updateRedirection, logged, failConnect } from '../actions/connexion.action';

import { Redirect } from 'react-router-dom';
import { isAdmin, isClient, isLoggingIn, isLoggedIn } from './authorizationHelper';
const jc = require('json-cycle');

//const dispatch = useDispatch();

const getRedirectionClientPage = async () => {
  console.log('In get redirect client');
  if (isLoggingIn()) {
    console.log('is logging')
    const isUserClient = await isClient();
    const isUserAdmin = await isAdmin();
    console.log('isclient value : '+isUserClient);
    if (isUserClient){
      console.log('Is a client');
      applicationStore.dispatch(logged('chef'));
      applicationStore.dispatch(updateRedirection(''));
    }
    else if (isUserAdmin) {
      console.log('The USER is an ADMIN');
      applicationStore.dispatch(logged('admin'));
      applicationStore.dispatch(updateRedirection('/adminConsole'));
    }
    else {
      console.log('CONNEXION FAILED, must return to connexion component');
      applicationStore.dispatch(failConnect());
      applicationStore.dispatch(updateRedirection('/connexion/client'));
    }
  }
  else if (!isLoggedIn()) {
    console.log('not logging');       // if the user didn't enter its credential we must display the connection page
    applicationStore.dispatch(updateRedirection('/connexion/client'));
  }
};

const getRedirectionAdminPage = async () => {
  console.log('In get redirect admin');
  if (isLoggingIn()) {
    const isUserClient = await isClient();
    const isUserAdmin = await isAdmin();
    if (isUserAdmin){
      console.log('the USEr is a ADMIN');
      applicationStore.dispatch(logged('admin'));
      applicationStore.dispatch(updateRedirection(''));
    }
    else if (isUserClient) {
      applicationStore.dispatch(logged('chef'));
      applicationStore.dispatch(updateRedirection('/clientConsole'));
    }
    else {
      console.log('CONNEXION FAILED, must return to connexion component');
      applicationStore.dispatch(failConnect());
      applicationStore.dispatch(updateRedirection('/connexion/client'));
    }
  }
  else if (!isLoggedIn()) {
    console.log('not logging');       // if the user didn't enter its credential we must display the connection page
    applicationStore.dispatch(updateRedirection('/connexion/admin'));
  }
};


// -------------------------------------------------------------------------------------------------------


const LoginRoute = (props) => {
  //console.log('IN LOGGING ROUTE + '+JSON.stringify(jc.decycle(props.component), null, 2));
  console.log('IN LOGGING ROUTE');
  if (isLoggingIn() == false) {
    console.log('Not logged in');
    return props.component;
  }
  else {
    console.log('Already logged in');
    return <Redirect to="/clientConsole" push/>;
  }
};

export { getRedirectionClientPage, getRedirectionAdminPage, LoginRoute };
