import React, { useState } from 'react';                    //REACT

import { useSelector, connect, useDispatch } from 'react-redux';     //REDUX
import { changeCurrentRestaurant, changePageMode, updateRestaurant } from '../../../actions/admin.action';    //ACTIONS

import { createAccountForRestaurant, updateRestaurantAccountCreated } from '../../../services/AdminAPI';    //API

import RestaurantForm from './RestaurantForm';  //COMPONENTS

import { Formik } from 'formik';    //FORMIK
import * as Yup from 'yup';         //YUP

import Row from 'react-bootstrap/Row'       //BOOTSTRAP
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'

import styles from './RestaurantDetails.module.css';

function RestaurantDetail(props) {

  const dispatch = useDispatch();
  const [createAccount, setCreateAccount] = useState(false);
  const [emailValue, setEmailValue] = useState('');
  const [passwordValue, setPasswordValue] = useState('');

  const restaurant = props.clientList[props.currentClient].contractList[props.contractIndex].restaurantList[props.restaurantIndex];

  const onClick = () => {
    console.log('In onclik change current restaurant, fetched : '+props.adminState.fetched+' fetchinf : '+props.adminState.fetching);
    dispatch(changeCurrentRestaurant(restaurant.nameRestaurant));
    dispatch(changePageMode('dashboard'));
  }

  const handleCreateAccount = () => {
    setCreateAccount(!createAccount);
  }

  const handleEmailChange = (event) => {
    setEmailValue(event.target.value);
  }

  const handlePasswordChange = (event) => {
    setPasswordValue(event.target.value);
  }

  const accountValidationSchema = Yup.object().shape({
    email: Yup.string().required('Ce champ est obligatoire'),
    password: Yup.string().required('Ce champ est obligatoire'),
  });

  const handleAddAccount = async (values, {setSubmitting, resetForm}) => {
    setSubmitting(true);
    console.log('In submit new account');
    createAccountForRestaurant(values.email, values.password, restaurant.nameRestaurant)
      .then( res => {
        console.log('In then create account : '+JSON.stringify(res, null, 2));
        updateRestaurantAccountCreated(restaurant.nameRestaurant)
          .then( res => {
            console.log('In then update restaurant : '+JSON.stringify(res, null, 2));
            dispatch(updateRestaurant(props.currentClient, props.contractIndex, props.restaurantIndex, props.clientList))
          })
          .catch()
      })
      .catch( err => {
        console.log('In create account catch error : '+JSON.stringify(err, null, 2));
      })
    setSubmitting(false);
  }

  return(
    <div className={styles.restaurantDetails}>
      <hr width="50%" />
      <Row>
        <Col>
          <h2> { restaurant.nameRestaurant } </h2>
        </Col>
        <Col>
          <h2> { restaurant.adressRestaurant } </h2>
        </Col>
        <Col>
          <Button onClick={onClick}> Voir le gaspillage </Button>
        </Col>
        <Col>
        </Col>
        {
          restaurant.accountCreated == false ?
            <Button onClick={handleCreateAccount}> Crer un compte </Button>
          :
            <Button disabled> Compte dj cr </Button>
        }
      </Row>

      {
        createAccount ?
          <Row>
            <Formik
              enableReinitialize
              initialValues={ { email: '',
                                password: '' } }
              validationSchema={ accountValidationSchema }
              onSubmit={ handleAddAccount }
            >
              { ({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => {
              return(
                <div>
                  <Form onSubmit={ handleSubmit }>
                    <Form.Row className='justify-content-md-center'>
                      <Form.Group as={ Col } md='4' controlId={ 'email' }>                   { /*NAME*/ }
                        <Form.Label> Email </Form.Label>
                        <Form.Control isValid={ touched.email && !errors.email }
                                      isInvalid={ touched.email && errors.email }
                                      placeholder='email'
                                      onBlur={ handleBlur }
                                      value={ values.email }
                                      onChange={ handleChange }>
                        </Form.Control>
                      </Form.Group>

                      <Form.Group as={ Col } md='4' controlId={ 'password' }>                   { /*ADRESS*/ }
                        <Form.Label> Mot de passe </Form.Label>
                        <Form.Control isValid={ touched.password && !errors.password }
                                      isInvalid={ touched.password && errors.adresspasswordRestaurant }
                                      placeholder='password'
                                      onBlur={ handleBlur }
                                      value={ values.password }
                                      onChange={ handleChange }>
                        </Form.Control>
                      </Form.Group>
                    </Form.Row>

                    <Row className='row justify-content-center'>
                      <Button variant='primary' type='submit' disabled={isSubmitting}> Valider </Button>
                    </Row>
                  </Form>
                </div>
              ) } }
            </Formik>
          </Row>
        :
          <> </>
      }
    </div>
  )
}

function mapStateToProps(state) {
  console.log('IN STATEMAP restaurant detail');
  return {
    clientList: state.adminReducer.clientList,
    currentClient: state.adminReducer.currentClient,
    adminState: state.adminReducer,
  }
}

export default connect(mapStateToProps)(RestaurantDetail);
