import { useSelector, useDispatch } from 'react-redux';   //REDUX
import applicationStore from '../store/store';            //store

import { updateCurrentRestaurantName } from '../actions/navigation.action';

import { getChefAccount } from '../services/AdminAPI';

const isLoggingIn = () => {
  console.log('In is logging in ?');
  const state = applicationStore.getState();
  const connexionState = state.userReducer;
  return connexionState.isLogging;
}

const isLoggedIn = () => {
  console.log('In is logged in ?');
  const state = applicationStore.getState();
  const connexionState = state.userReducer;
  return connexionState.isLogged;
}


const isClient = async () => {
  console.log('In is client ?');
  const store = applicationStore.getState();
  const accounts = await getChefAccount(store.userReducer.login, store.userReducer.password);
  console.log('ACCOUNTS : '+JSON.stringify(accounts, null, 2));

  if (accounts[0] != null){
    applicationStore.dispatch(updateCurrentRestaurantName(accounts[0].restaurantName))
    console.log('IS A CLIENT')
    return true;
  }
  else {
    console.log('NOT A CLIENT')
    return false;
  }
}

const isAdmin = () => {
  const state = applicationStore.getState();
  const connexionState = state.userReducer;
  if (connexionState.login=='admin' && connexionState.password=='adminMdp'){
    console.log('IS A ADMIN')
    return true;
  }
  else {
    console.log('NOT A ADMIN')
    return false;
  }
}

export { isLoggingIn, isLoggedIn, isClient, isAdmin };

