import React, { useEffect }  from 'react';

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX
import { getAllClientsSuccess, getAllClientsError, getAllClientStart } from '../../../actions/admin.action';  //ACTIONS

import { getAllClients } from '../../../services/AdminAPI';           //API FUNCTIONS

import Row from 'react-bootstrap/Row'   //BOOTSTRAP
import Col from 'react-bootstrap/Col'

import AdminSideBar from '../topBar/AdminSideBar';    //COMPONENTS
import AdminGaspi from '../gaspillage/AdminGaspi';
import ClientDashboard from '../../client/dashboard/ClientDashboard';
import NewClientForm from '../home/NewClientForm';
import ClientDetails from '../home/ClientDetails';

function AdminContainer(props) {

  const dispatch = useDispatch();

  useEffect( () => {
    console.log('Use effect admin container, fetched : '+props.adminState.fetched+' fetching : '+props.adminState.fetching);
    if (!props.adminState.fetched && !props.adminState.fetching) {
      dispatch(getAllClientStart());
      getAllClients()
        .then(res => {
          dispatch(getAllClientsSuccess(res.result));
        })
        .catch(err => {
          dispatch(getAllClientsError(err));
        })
    }
  });

  const displayPage = (pageMode) => {
    console.log('IN admin display page function = '+pageMode + ' fetched : '+props.adminState.fetched);
    switch (pageMode) {
      case 'displayClient':
        console.log('IN client');
        return <ClientDetails client={ props.adminState.currentClient } />;
        break;
      case 'createClient':
        console.log('IN client');
        return <NewClientForm />;
        break;
      case 'dashboard':
        console.log('IN DASHBOARD, fetched : '+props.adminState.fetched);
        return <ClientDashboard restaurantName={props.adminState.currentRestaurant}/>;
        break;
      case 'stat':
        console.log('IN STAT');
        return <AdminGaspi restaurantName={props.adminState.currentRestaurant} />;
        break;
      case 'advice':
        console.log('IN advice');
        return <h1>NOT READY YET</h1>;
        break;
      case 'restaurant':
        return (<h1>NOT READY YET</h1>);
        break;
      default:
        return <NewClientForm />;
    }
  }

  return(
    <>
      <Row style={{ height:'100vh', margin: 0, padding: 0}}>

        <Col md='2' className='fixed-top' style={{ margin: 0, padding: 0}}>
          <AdminSideBar />
        </Col>

        <Col md={{span: 10, offset: 2}} style={{padding: '0px' }}>
          {
            displayPage(props.pageMode)
          }
        </Col>

      </Row>
    </>
  )
}


function mapStateToProps(state) {
  //console.log('WTF : '+JSON.stringify(state, null, 2));
  console.log('IN admin container map to state : '+state.adminReducer.pageMode +' fetched : '+state.adminReducer.fetched + ' pageMode : '+state.adminReducer.pageMode);
  return {
    pageMode: state.adminReducer.pageMode,
    adminState: state.adminReducer,
  }
}

export default connect(mapStateToProps)(AdminContainer);
