const initialState = {
  login: null,
  password: null,
  userType: null,     //can be 'chief' or 'director'
  isLogged: false,
  isLogging: false,
  redirection: null,
};

const userReducer = (state = initialState, action) => {

  switch(action.type){
    case 'CONNECTING':
      console.log('USER REDUCER connecting');
      return {
        ...state,
        isLogging: true,
        login: action.payload.login,
        password: action.payload.password
      }
      break;

    case 'FAILED_CONNECT':
      console.log('USER REDUCER failed connect');
      return {
        ...state,
        isLogging: false,
        login: null,
        password: null
      }
      break;

    case 'UPDATE_REDIRECTION':
      console.log('USER REDUCER update direction = '+action.payload.redirection);
      return {
        ...state,
        redirection: action.payload.redirection,
      }
      break;

    case 'LOGGED':
      console.log('USER REDUCER logged');
      return {
        ...state,
        isLogged: true,
        isLogging: false,
        userType: action.payload.userType,
      }
      break;

    case 'DISCONNECT':
      console.log('In disconnect user')
      return initialState;
      break;

    default:
      return state;
      break;
  }
}

export default userReducer;
