const initialState = {
  pageMode: 'dashboard',
  currentRestaurantName: '',
};

const navigationReducer = (state = initialState, action) => {

  switch(action.type){
    case 'UPDATE_PAGEMODE':
      console.log('nav reducer update pagemode');
      return {
        ...state,
        pageMode: action.payload.pageMode,
      }
      break;

    case 'UPDATE_CURRENT_RESTAURANT_NAME':
      console.log('nav reducer update current restaurant name');
      return {
        ...state,
        currentRestaurantName: action.payload.name,
      }
      break;

    case 'DISCONNECT':
      console.log('In disconnect navigation')
      return initialState;
      break;

    default:
      return state;
      break;
  }
}

export default navigationReducer;
