const initialState = {
  currentRestaurant: null,
  currentContract: null,
  page: 'dashboard',
  drillDownLevel: 'year',
  selectedMonth: null,
  selectedWeek: null,
  selectedDay: null,
  selectedCategory: null,
  dataType: 'mass',
  fetching: false,
  fetched: false,
  error: false,
  rawData: null,
  clearData: null,
  displayedData: null
}

const clientReducer = (state = initialState, action) => {
  //console.log('TEST : '+JSON.stringify(state, null, 2));
  switch(action.type){

    case 'CHANGE_PAGE':
      return {
        ...state,
        page: action.data.newPage
      };
      break;

    case 'GET_RAW_DATA_START':
      console.log('in reducer get raw data start, state = '+JSON.stringify({...state, fetching: true }, null, 2));
      return {
        ...state,
        fetching: true
      };
      break;

    case 'GET_RAW_DATA_SUCCESS':
      return {
        ...state,
        fetching: false,
        fetched: true,
        rawData: action.data,
      };
      break;

    case 'GET_RAW_DATA_ERROR':
      return {
        ...state,
        fetching: false,
        fetched: false,
        error: action.error
      }
      break;

    case 'UPDATE_DRILLDOWN':
    console.log('Update drillDownLevel');
      return {
        ...state,
        drillDownLevel: action.data
      }
      break;

    case 'UPDATE_DATA_TYPE':
      console.log('NEW DATATYPE : '+action.data);
      return {
        ...state,
        dataType: action.data
      }
      break;

    case 'UPDATE_MONTH':
      return {
        ...state,
        selectedMonth: action.data
      }
      break;

    case 'UPDATE_WEEK':
      return {
        ...state,
        selectedWeek: action.data
      }
      break;

    case 'UPDATE_DAY':
      return {
        ...state,
        selectedDay: action.data
      }
      break;

    case 'UPDATE_CATEGORY':
      return {
        ...state,
        selectedCategory: action.data
      }
      break;

    case 'DISCONNECT':
      console.log('In disconnect client')
      return initialState;
      break;

    default:
      return state;
      break;
  }
}

export default clientReducer;
