import React, { useState } from 'react';                //REACT

import { useSelector, useDispatch, connect } from 'react-redux';           //REDUX

import { changeModeToCreate, changeCurrentClient } from '../../../actions/admin.action';  //ACTIONS

import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';

import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'

import styles from './ClientList.module.css'    //CSS

function AdminHome(props) {

  const dispatch = useDispatch();
  const [displayList, setDisplayList] = useState(false);
  const [returnNormal, setReturnNormal] = useState(false);

  const newCurrentClient = (event, newCurrentClientIndex) => {
    event.preventDefault();
    console.log('IN change current');
    dispatch(changeCurrentClient(newCurrentClientIndex));
  };

  const addNewClient = (event) => {
    event.preventDefault();
    console.log('IN create new client');
    dispatch(changeModeToCreate());
  };

  const selectToggle = (event) => {
    setReturnNormal(displayList);
    setDisplayList(!displayList);
  }

  console.log('Before render client list');
  return(
    <Row>
        <Accordion className={styles.accordion}>
        
          <Accordion.Toggle as={Col} eventKey="0" className={styles.toggleElement} onClick={selectToggle}>
            <Row >
              <Col md='6'> <strong> Client list </strong> </Col>
              <Col md={{offset: 4}} > <strong className={displayList ? styles.active : styles.unactive }> &#8250; </strong> </Col>
            </Row>
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="0" className={styles.collapseElement}>
            <ul style={{listStyleType: 'none'}}>
              {
                props.clientList.map( (item, index) => (
                  <li onClick={ (event) => newCurrentClient(event, index) } className={styles.clientElement}> { item.NameClient } </li>
                ))
              }
              <li onClick={ addNewClient }> + Add new client </li>
            </ul>
          </Accordion.Collapse>

        </Accordion>
    </Row>
  )
}

export default AdminHome;
