export async function getChefAccount (login, password) {
  console.log('In chef get account');
  const url = back_url+'/account';
  const res = await axios.get(url, { params: { login: login, password: password, type: 'chef' } } )
    .then (res => {
      //console.log('In then getChefAccount : '+JSON.stringify(res, null, 2));
      return res.data.result;
    })
    .catch ( err => {
      console.log('In catch getChefAccount : '+err);
    })
  console.log('Result : '+JSON.stringify(res, null, 2));
  return res;
}

/**
 *
 *
 */
 export function createAccountForRestaurant (email, password, restaurantName) {
   const data = { email: email, password: password, nameRestaurant: restaurantName, accountType: 'chef' };
   const url = back_url+'/account';
   return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => {
        console.log('IN THEN IN API : '+JSON.stringify(response, null, 2));
        resolve(response.data)
      })
      .catch(err => {
        console.log('In catch in api : '+err)
        reject(err)
      })
  })
}

/**
 *
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function updateUserPassword (old, newPassword, email) {
   const data = { old: old, new: newPassword, email: email };
   const url = back_url+'/account';
   return new Promise((resolve, reject) => {
    axios.put(url, data)
      .then(response => {
        console.log('IN THEN IN API : '+JSON.stringify(response, null, 2));
        sleep(5000);
        resolve(response.data)
      })
      .catch(err => {
        console.log('In catch in api : '+err)
        sleep(5000);
        reject(err)
      })
  })
}
function sleep(milliseconds) {
  const date = Date.now();
  let currentDate = null;
  do {
    currentDate = Date.now();
  } while (currentDate - date < milliseconds);
}

