/**
 *  GET all the clients in the database
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function getAllClients () {
  return new Promise((resolve, reject) => {
    axios.get(back_url+'/clients')
      .then(response => resolve(response.data))
      .catch(err => reject(err))
  })
}

/**
 *  POST create a new client in the database
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function createClient (newClient) {
  return new Promise((resolve, reject) => {
    axios.post(back_url+'/client', newClient )
      .then(response => resolve(response.data.result))
      .catch(err => reject(err))
  })
}

//-------------------------------------------------- CONTRACT -----------------------------------------------

/**
 *  GET all the contracts for a client
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function getAllContracts (nameClient) {
  return new Promise((resolve, reject) => {
    axios.get(back_url+'/client/'+nameClient+'/contracts')
      .then(response => resolve(response.data))
      .catch(err => reject(err))
  })
}

/**
 *  POST create a new contract for a client
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function createContract (nameClient, newContract) {
  const url = back_url+'/client/'+nameClient+'/contract';
  return new Promise((resolve, reject) => {
    axios.post(url, newContract )
      .then(response => resolve(response.data.result))
      .catch(err => reject(err))
  })
}

