/**
 *  POST create a new contract for a client
 * @returns {Promise<unknown>} a Promise which contains a JSON
 */
 export function createRestaurant (nameContract, newRestaurant) {
  newRestaurant.accountCreated = false;
  const url = back_url+'/contract/'+nameContract+'/restaurant';
  return new Promise((resolve, reject) => {
    axios.post(url, newRestaurant)
      .then(response => resolve(response.data.result))
      .catch(err => reject(err))
  })
}

