import React from 'react';
import { ResponsiveLine } from '@nivo/line';
import styles from './Graph.module.css'; //CSS

// make sure parent container have a defined height when using responsive component,
// otherwise height will be 0 and no chart will be rendered.
// website examples showcase many properties, you'll often use just a few of them.
const Line = (props) => {

  const toolTipElement = (tooltipProps) => {
    console.log('IN TOOLTIP');

    var foodIndex = props.allData.map( item => item.id ).indexOf(tooltipProps.point.serieId);
    console.log('FOOD INDEX  = '+foodIndex);
    var dateIndex = props.allData[foodIndex].data.map( item => item.x ).indexOf(tooltipProps.point.data.x);
    console.log('THE INDEX : ' + dateIndex);

    return (
      <>
      <div className={styles.tooltip}>
        { tooltipProps.point.data.y }
      </div>
      {
        /*
        <div style={{backgroundColor: 'white'}}>
          <> { tooltipProps.point.serieId } </>
          <br/>
          <>{ tooltipProps.point.data.x } : </> <> { tooltipProps.point.data.y } </>
          <br/>
          <> Moyenne : </> <> { tooltipProps.point.data.y==0 ? 0 :(tooltipProps.point.data.y / props.allData[foodIndex].data[dateIndex].tray).toFixed(2) } </>
        </div>
        */
      }
      </>
    )
  };

  console.log('RERENDER THE LINE : '+JSON.stringify(props.data, null, 2));

  return(
    <ResponsiveLine
    tooltip={toolTipElement}
    isInteractive={true}
    useMesh={true}
    debugMesh={false}
    data={props.data}
    margin={{ top: 20, right: 50, bottom: 80, left: 60 }}
    xScale={{ type: 'point' }}
    yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: false, reverse: false }}
    curve="linear"
    axisTop={null}
    axisRight={null}
    axisBottom={{
        orient: 'bottom',
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: 'Temps',
        legendOffset: 36,
        legendPosition: 'middle'
    }}
    axisLeft={{
        orient: 'left',
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: props.dataType,
        legendOffset: -40,
        legendPosition: 'middle'
    }}
    colors={{ scheme: 'nivo' }}
    pointSize={11}
    pointColor={{ theme: 'background' }}
    pointBorderWidth={3}
    pointBorderColor={{ from: 'serieColor', modifiers: [] }}
    pointLabel="y"
    pointLabelYOffset={-12}
    useMesh={true}
    legends={[
        {
            anchor: 'bottom-left',
            direction: 'row',
            justify: false,
            translateX: 0,
            translateY: 72,
            itemsSpacing: 0,
            itemDirection: 'left-to-right',
            itemWidth: 80,
            itemHeight: 20,
            itemOpacity: 0.75,
            symbolSize: 12,
            symbolShape: 'circle',
            symbolBorderColor: 'rgba(0, 0, 0, .5)',
            effects: [
                {
                    on: 'hover',
                    style: {
                        itemBackground: 'rgba(0, 0, 0, .03)',
                        itemOpacity: 1
                    }
                }
            ]
        }
    ]}
/>

);
}

export default Line;


